
.around-container{
	width: 90%;
	height: 686px;
	margin: 0 auto;
	background: #f5f5f5;
}
.goods-content{
	width: 100%;
	height: auto;
	margin: 0 auto;
}
.goods-top{
	width: 100%;
	height: 58px;
}
.goods-top .top-title{
	float: left;
	margin: 0;
	font-size: 22px;
	font-weight: 200;
	line-height: 58px;
	color: #333333;
}
.goods-top .top-sub{
	float: right;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	color: #424242;
	margin-top: 16px;
	font-size: 16px;
	-webkit-box-orient:horizontal;
	-webkit-box-direction:normal;
	    -ms-flex-flow:row nowrap;
	        flex-flow:row nowrap;
	-webkit-box-align:center;
	    -ms-flex-align:center;
	        align-items:center;
	-webkit-box-pack:start;
	    -ms-flex-pack:start;
	        justify-content:flex-start;
}
.goods-top .top-sub li{
	width: auto;
	height: 24px;
	line-height: 24px;
	margin-left: 30px;
	border-bottom: 2px solid #f5f5f5;
	cursor: pointer;
}
.goods-top .top-sub .active{
	color: #ff6700;
	border-bottom: 2px solid #ff6700;
}



