
.xm-top-sub {
	position: relative;
	width: 100%;
	height: 170px;
}
.xm-top-sub-son {
	position: relative;
    width: 90%;
    height: auto;
    margin: 14px auto 26px;
}
.sub-1 {
	float: left;
	width: 20%;
    height: 170px;
	background: #5f5750;
}
.channels {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: horizontal;
	-webkit-box-direction: normal;
	    -ms-flex-flow: row wrap;
	        flex-flow: row wrap;
	-webkit-box-pack: start;
	    -ms-flex-pack: start;
	        justify-content: flex-start;
	-webkit-box-align: start;
	    -ms-flex-align: start;
	        align-items: flex-start;
	list-style: none;
	margin: 0;
	padding: 3px;
}
.channel {
    width: 30%;
    height: 82px;
    cursor: pointer;
    margin-left: 6px;
}
.channel:hover .channel-txt {
	color: #fff;
}
.channel:hover	.sub-icon {
	 color: #fff;
}
.channel-txt {
	display: block;
    margin: 0;
	width: 75px;
	font-size: 12px;
	text-align: center;
	text-decoration: none;
	color: rgba(255,255,255,0.7);
}
.sub-icon {
	width: 75px;
	height: 24px;
	margin-top: 18px;
	margin-bottom: 5px;
	font-size: 20px;
	color: rgba(255,255,255,0.7);
}
.subs {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: horizontal;
	-webkit-box-direction: normal;
	    -ms-flex-flow: row nowrap;
	        flex-flow: row nowrap;
	-webkit-box-pack: start;
	    -ms-flex-pack: start;
	        justify-content: flex-start;
	margin: 0;
	padding: 0;
	list-style: none;
}
.subs li {
    width: 31%;
    height: 170px;
    margin-left: 26px;
}
.sub-item {
	display: block;
	-webkit-transition: all 0.3s;
	transition: all 0.3s;
}
.sub-item:hover {
	 -webkit-box-shadow: 0 5px 30px #ccc;
	         box-shadow: 0 5px 30px #ccc;
}
.subs  img {
	display: block;
	width: 100%;
	height: 170px;
}
. {
	width: 1px;
	height: 60px;
	margin-top: 10px;
	background: rgba(255,255,255,0.1);
}
.line-horizontal {
    width: 24%;
    height: 1px;
    margin: 0 11px;
    background: rgba(255,255,255,0.1);
}
.line-vertical{
    width: 1px;
    height: 60px;
    margin-top: 10px;
    background: hsla(0,0%,100%,.1);
}
.fa-mobile-phone:before, .fa-mobile:before {
    content: "\F10B";
}
.fa-gift:before {
    content: "\F06B";
}
 





