
.regist_all{
  position: absolute;
  width: 100%;
  height: 100%;
  background: #f9f9f9;
  color: #666;
}
.mi_regist{
  width: 60%;
  margin:0 auto;
  height: 100%;
  background: #fff;
}
.mi_regist .form{
  width: 50%;
  margin:auto;
  height: auto;
  position: relative;
  top: 15%;
}
.regist_all .el-select{
  width: 100%;
}
.mi_regist .img img{
  margin: 0 auto;
  display: block;
}
.mi_regist .title h3{
  text-align: center;
}
.mi_regist .img,.mi_regist .title{
  margin-bottom: 10%;
}
.regist_all .el-button--primary{
  width: 100%;
  background-color: #ff6700;
  border: none;
}
.regist_all .el-button--primary:hover {
    background: #ff936a;
}
.regist_all .region p{
  margin: 0
}
.regist_all .verify input{
  width: 45%;
}
.phone{
  margin-bottom: 22px;
}
.phone .inp1{
  width: 30%;
  float: left;
}
.phone .inp1 input{
  border-right: none;
  border-bottom-right-radius: 0;
  border-top-right-radius: 0;
}
.phone .inp2{
  width: 70%;
}
.phone .inp2 .el-input__inner{
  border-radius: 0
}
.verify #v_container{
  width: 200px;
  height: 50px;
  float: right;
}
.verify #v_container canvas{
  height: 40px;
  float: right;
}




