
.jm_footer-wrap a{
		text-decoration: none;
}
.jm_footer-wrap{
		width: 100%;
		height: auto;
		background-color: #fff;
}
.jm_footer-wrap .footer{
		width: 90%;
		height: auto;
		margin: 0 auto;
		margin-bottom: 20px;
}
.jm_footer-wrap .footer-service{
		padding:27px 0;
		border-bottom: 1px solid #e0e0e0;
}
.jm_footer-wrap .service .service-item{
		float: left;
		width: 20%;
		height: 27px;
		line-height: 27px;
		font-size: 16px;
		color: #616161;
		padding: 0 40px;
		-webkit-box-sizing:border-box;
		        box-sizing:border-box;
		border-right: 1px solid #e0e0e0;
}
.jm_footer-wrap .service .service-item:nth-last-child(1){
		border-right: 0;
}
.jm_footer-wrap .service .service-item .icon{
		display: inline-block;
		height: 27px;
		line-height: 27px;
		font-size: 24px;
		margin-right: 10px;
}
.jm_footer-wrap .footer-info{
		padding: 20px 0;
}

	/*.jm_footer-wrap .footer-content .footer-item:first-child{
		margin-left: 0;
	}*/
.jm_footer-wrap .footer-content .footer-item{
		float: left;
		width: 13.4%;
		height: auto;
		text-align: left;
}
.jm_footer-wrap .footer-content .footer-item a{
		text-decoration: none;
		color: gray;
}
.jm_footer-wrap .footer-content .footer-item:nth-last-child(2) .col-links{
		padding-right: 20px;
		border-right: 1px solid #e0e0e0;
}
.jm_footer-wrap .footer-content .footer-item h3{
		font-size: 14px;
		font-weight: normal;
		margin-bottom: 26px;
}
.jm_footer-wrap .footer-content .footer-item p{
		font-size: 12px;
		margin-top: 10px;
}
.jm_footer-wrap .footer-last-item{
		/*margin-left: 3%;*/
		margin-top: 20px;
		width: 16%;
		float:left;
}
.jm_footer-wrap .footer-last-item .col-content{
		margin-left: 40px;
}
.jm_footer-wrap .footer-last-item .col-content .phone-number{
		margin: 0 0 5px;
		font-size: 22px;
		/*text-align: center;*/
		margin-bottom: 5px;
		color: #ff6700;
}
.jm_footer-wrap .footer-last-item .col-content .phone-desc{
		font-size: 12px;
		line-height: 1.5;
		/*text-align: center;*/
		color: #616161;
}
.jm_footer-wrap .footer-last-item .col-content .server-btn{
		display: block;
		width: 120px;
		height: 30px;
		line-height: 30px;
		font-size: 12px;
		/*text-align: center;*/
		margin: 0 auto;
		border: 1px solid red;
		color: #ff6700;
		cursor: pointer;
		margin-left: 1%;
}
.jm_footer-wrap .footer-last-item .col-content .server-btn:hover{
		background: #ff6700;
		color: #fff;
}
.jm_footer-wrap .footer-explan .logo-img{
		float: left;
		width: 57px;
		height: 57px;
}
.jm_footer-wrap .explan-content{
		float: left;
		padding-left: 10px;
}
.jm_footer-wrap .explan-content .links .link-item{
		float: left;
		padding: 0 5px;
		font-size: 12px;
		color: red;
		border-right: 1px solid #757575;
}
.jm_footer-wrap .explan-content .links .link-item:nth-last-child(1){
		border-right: 0
}
.jm_footer-wrap .explan-txt a{
		text-decoration: none;
		color: #b0b0b0;
}
.jm_footer-wrap .explan-txt a:hover{
		color: red;
}
.jm_footer-wrap .logo-explan{
		width: 57px;
		height: 57px;
}
.jm_footer-wrap .logo-explan .logo-explan-first{
		margin-left: 50px;
}
.jm_footer-wrap .site-info{
		padding: 30px 0;
}

/*	.jm_footer-wrap .explan-content{
		width: 65%
	}*/
.jm_footer-wrap .explan-txt{
		margin: left;
		line-height: 18px;
		font-size: 12px;
		color: #b0b0b0;
}

/*	.footer-introduce-logo{
		width: 20%
	}
*/
.footer-introduce-logo .logo-explan{
		width: 85px;
		height: 28px;
}
.jm_footer-wrap .site-info .footer-desc{
		width: 267px;
		height: 19px;
		margin: 30px auto 0;
		text-align: left;
		overflow: hidden;
		background: url('http://s01.mifile.cn/i/slogan2016.png') center center no-repeat;
}




