
ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
.clearfix:after {
  content: ".";
  display: block;
  height: 0;
  clear: both;
  visibility: hidden
}
.clearfix {
  *+height: 1%;
}
a {
  text-decoration: none;
  color: #333;
}
.box-container{
	width: 100%;
	height: auto;
	background-color: #f5f5f5;
	/*margin-bottom: 26px;*/
	padding-top: 60px;
}
.boxs{
	width: 90%;
	height: auto;
	margin: 0 auto;
	position: relative;
}
.boxs-top{
	width: 100%;
	height: 58px;
}
.top-title{
	color: #333333;
	float: left;
	font-size: 22px;
	font-weight: 200;
	line-height: 58px;
	margin:0;
}
.top-sub{
	color: #424242;
	float: right;
	display: block;
	font-size: 16px;
	line-height: 58px;
	cursor: pointer;
}
.top-sub:hover{
	color: #ff6700;
}
.sub-icon{
	color: #b0b0b0;
	font-size: 20px;
}
.sub-icon:hover{
	color: #ff6700;
}
.box-left{
	float: left;
	width: 20%;
	height: 614px;
	cursor: pointer;
	-webkit-transition: all 0.3s;
	transition:all 0.3s;
}
.box-left img{
	width: 100%;
	height: 614px;
}
.box-left:hover,.box-item:hover{
	-webkit-transform: translateY(-1px);
	        transform: translateY(-1px);
	-webkit-box-shadow: 5px 5px 20px #ccc;
	        box-shadow: 5px 5px 20px #ccc;
}
.box-right{
	float: left;
	width: 80%;
	height: 628px;
}
.box-list{
	width: 100%;
	height: 100%;
	background: #f5f5f5;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-pack:start;
	    -ms-flex-pack:start;
	        justify-content:flex-start;
	-webkit-box-orient:horizontal;
	-webkit-box-direction:normal;
	    -ms-flex-flow:row wrap;
	        flex-flow:row wrap;
	list-style: none;
}

