.box-item{
    width: 23%;
    height: auto;
    padding: 20px 0;
    background: #fff;
    position: relative;
    margin-left: 2%;
    margin-bottom: 16px;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
}
.discount-label{
	width: 64px;
	height: 20px;
	line-height: 20px;
	position: absolute;
	top: 0;
	left: 50%;
	margin-left: -32px;
	font-size: 12px;
	color: #fff;
	text-align: center;
	z-index: 4;
}
.free { background-color: #ffac13;
}
.new { background-color: #83c44e;
}
.discount { background-color: #e53935;
}
.old-price{
	color: #b0b0b0;
	text-decoration: line-through;
}
.box-right img{
	display: block;
	width: 160px;
	height: 160px;
	margin: 0 auto;
}
.box-title{
	font-size: 14px;
	color: #333;
	text-align: center;
	font-weight: normal;
}
.box-desc{
	height: 18px;
	font-size: 12px;
	text-align: center;
	color: #b0b0b0;
	overflow: hidden;
	margin: 0 10px 10px;
	text-overflow:ellipsis;
	white-space: nowrap;
}
.box-price{
	color: #ff6700;
	font-size: 14px;
	text-align: center;
}


