
.xm-video-container {
  width: 90%;
  height: auto;
  margin: 0 auto;
}
.header1 {
  position: relative;
  width:100%;
  height:59px;
}
.header1 .title {
    display: block;
    float: left;
    margin: 0;
    font-size: 22px;
    font-weight: 200;
    line-height: 58px;
    color: #333;
}
.top-sub1 {
    float: right;
    display: block;
    font-size: 16px;
    line-height: 58px;
    color: #424242;
    cursor: pointer
}
.top-sub1:hover {
    color: #ff6700;
}
.header1 .top-sub1:hover  .sub-icon-v {
    color: #ff6700;
}
.header1 .top-sub1 .sub-icon-v {
   color: #b0b0b0;
   font-size: 20px;
}
.video-wrap-v .video-item {
    position: relative;
    float: left;
    width: 24%;
    height: 285px;
    margin: 0 0 14px 14px;
    background: #fff;
    cursor: pointer;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
    list-style:none;
}
.video-item:hover {
    -webkit-transform: translateY(-3px);
            transform: translateY(-3px);
    -webkit-box-shadow: 5px 5px 20px #ccc;
            box-shadow: 5px 5px 20px #ccc;
}
.video-item:hover .play {
    background: #ff6700;
}
.video-wrap-v .video-item:nth-child(1) {
    margin-left: 0;
}
.video-wrap-v .video-item   img {
    width: 100%;
    height: 180px;
    margin-top: -10px;
}
.video-wrap-v .video-item   .video-title {
    color: #333;
    font-size: 14px;
    font-weight: normal;
    text-align: center;
}
.video-wrap-v .video-item  .video-desc {
    height: 18px;
    margin: 0;
    font-size: 12px;
    text-align: center;
    color: #b0b0b0;
}
.item-top {
    position: relative;
}
.item-top   .play {
    position: absolute;
    display: block;
    left: 20px;
    bottom: 10px;
    width: 32px;
    height: 20px;
    line-height: 20px;
    color: #fff;
    text-align: center;
    font-size: 12px;
    border: 2px solid #fff;
    border-radius: 12px;
    background: #333;
}




