
.jm_smart{
		height: 820px;
		background: #f8f8f8;
}
.smart-container{
		padding-top: 130px;
		text-align: center;
}
.smart-container h2{
		line-height: 64px;
		font-size: 40px;
		font-weight: normal;
		font-family: '仿宋';
		opacity: 0;
		-webkit-transition: all 2s;
		transition: all 2s;
		top: 20%;
}
.smart-container .item-des{
		margin-top: 10px;
		line-height: 30px;
    	font-size: 16px;
    	opacity: 0;
    	-webkit-transition: all 3.5s;
    	transition: all 3.5s;
		/*top: 20%;*/
		margin-top: -5%;
}
.smart-content{
		max-width: 1920px;
		margin: 70px auto 0;
		height: 500px;
}
.smart-list{
		margin: 0;
		padding: 0;
		line-height: 1;
		border: none;
}
.jm_smart .group{
		float: left;
		width: 33.3%;
		padding-bottom: 60px;
		font-family: '楷书';
		padding-top: 20px;
}
.smart-list li{
		list-style: none;
		display: list-item;
}
.jm_smart span{
		display: block;
		font-size: 16px;
}
.jm_smart .group .icon{
		background-image: url(//c1.mifile.cn/f/i/17/mitv4/65index/icon-smart.png);
		margin: 0 auto 30px;
    	width: 70px;
    	height: 70px;
    	background-repeat: no-repeat;
}
.jm_smart .group .icon01{
		opacity: 0;
		-webkit-transition: all 1s .4s;
		transition: all 1s .4s;
		background-position: 0px 0;
		top: 20%;
}
.jm_smart .group .icon02{
		opacity: 0;
		-webkit-transition: all 1s .4s;
		transition: all 1s .4s;
		background-position: -70px 0;
		top: 20%;
}
.jm_smart .group .icon03{
		opacity: 0;
		-webkit-transition: all 1s .4s;
		transition: all 1s .4s;
		background-position: -140px 0;
		top: 20%;
}
.jm_smart .group .icon04{
		opacity: 0;
		-webkit-transition: all 1s 1s;
		transition: all 1s 1s;
		top: 20%;
		background-position: -210px 0;
}
.jm_smart .group .icon05{
		opacity: 0;
		-webkit-transition: all 1s 1s;
		transition: all 1s 1s;
		top: 20%;
		background-position: -280px 0;
}
.jm_smart .group .icon06{
		opacity: 0;
		-webkit-transition: all 1s 1s;
		transition: all 1s 1s;
		top: 20%;
		background-position: -350px 0;
}
.jm_smart .small-title{
		padding-bottom: 15px;
		font-size: 24px;
}
.jm_smart .small-title01,.jm_smart .small-title02,.jm_smart .small-title03{
		-webkit-transition: all 5s;
		transition: all 5s;
		opacity: 0;
		top: 20%;
}
.jm_smart .small-title04,.jm_smart .small-title05,.jm_smart .small-title06{
		-webkit-transition: all 1s .1.2s;
		transition: all 1s .1.2s;
		opacity: 0;
}
.jm_smart .tips{
		-webkit-transition: all 1s .8s;
		transition: all 1s .8s;
}
.jm_smart .tips01,.jm_smart .tips02,.jm_smart .tips03{
		-webkit-transition: all 1s .8s;
		transition: all 1s .8s;
}
.jm_smart .tips04,.jm_smart .tips05,.jm_smart .tips06{
		-webkit-transition: all 1s 1.4s;
		transition: all 1s 1.4s;
}
.jm_smart .line{
		margin: 30px auto 0;
    	width: 50%;
    	height: 2px;
    	background: #e6e6e6;
    	opacity: 0;
    	filter: alpha(opacity=0)\9;
    	-webkit-transition: all 1s 1.5s;
    	transition: all 1s 1.5s;
}
.jm_smart .show{
		opacity: 1;
}





