
.jm_footer.preload {
    	background: url(//c1.mifile.cn/f/i/17/mitv4/65index/bg-footer.jpg) no-repeat center center;
}
.jm_footer{
		height: 300px;
}
.jm_footer .link-a{
		display: block;
		width: 100%;
		height: 100%;
		color: #fff;
}
.jm_footer .content{
		padding-top: 100px;
    	text-align: center;
}
.jm_footer .content h2,.jm_footer .content .item-des{
		opacity: 0;
		-webkit-transition: all 2s;
		transition:all 2s;
		margin-top: 20px;
}
.jm_footer-erweima{
		background: #fff;
    	padding: 45px 0;
}
.jm_footer-erweima .container{
		width: 90%;
    	margin-right: auto;
    	margin-left: auto;
}
.common-erweima-content{
		margin: 0 auto;
    	width: 700px;
}
.common-erweima-pic{
		float: left;
    	position: relative;
    	margin: 0 30px;
    	width: 136px;
    	height: 136px;
}
.common-erweima-pic img{
		position: absolute;
    	left: 0;
    	top: 0;
    	right: 0;
    	bottom: 0;
   		margin: auto;
}
.common-erweima-context{
		float: left;
    	padding: 10px 30px;
}
.common-erweima-context .tips{
		padding: 15px 0;
    	font-size: 30px;
}
.common-erweima-context .tips span{
		color: #ff3333;
}
.jm_footer-tips{
		background: url(//c1.mifile.cn/f/i/17/mitv4/65index/footer-newtips.jpg) no-repeat center center;
		height: 80px;
}



