
.qy_topBanner{
      width: 100%;
      height: 460px;
      /*background: yellow;*/
      margin: 0 auto;
}
.topBanner_all{
      position: relative;
      /*display: flex;*/
      height: 100%;
      /*background: green;*/
      margin:  auto;
      width: 90%;
}
.topBanner_all .qy_lunboLeft{
      position: absolute;
      width: 20%;
      background: #757575;
      height: 460px;
      z-index: 999;
}
.el-carousel__arrow--left {
      left: 20%;
}
.topBanner_all .qy_lunboLeft ul{
      padding: 20px 0;
      text-align: center;
}
.topBanner_all .qy_lunboLeft li{
      width: 100%;
      height: 42px;
      line-height: 42px;
      cursor: pointer;
}
.topBanner_all .qy_lunboLeft li:hover{
      background: #ff6700;
}
.topBanner_all .qy_lunboRight{
      position: absolute;
      top: 0;
      right:0;
      width: 100%;
      height: 460px;
}
.category-detail{
    	position: absolute;
    	z-index: 999;
    	left: 235px;
    	height: 460px;
    	width: auto;
    	background: #fff;
    	display: -webkit-box;
    	display: -ms-flexbox;
    	display: flex;
    	/*flex-wrap:row nowrap;
    	justify-content: flex-start;*/
      border: 1px solid #ccc;
}
.category-items {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
    	-webkit-box-orient: vertical;
    	-webkit-box-direction: normal;
    	    -ms-flex-flow: column wrap;
    	        flex-flow: column wrap;	     
	    height: 460px;
		  background: #fff;
}
.category-detail .category-goods{
    	width: 265px;
    	height: 76.6px;
    	display: -webkit-box;
    	display: -ms-flexbox;
    	display: flex;
    	/*flex-wrap:column  wrap;*/
    	-ms-flex-wrap:row nowrap;
    	    flex-wrap:row nowrap;   	
		  -webkit-box-align: center;   	
		      -ms-flex-align: center;   	
		          align-items: center;
		/*justify-content: flex-start;*/
		  -webkit-box-sizing: border-box;
		          box-sizing: border-box;
}
.category-detail .category-goods .goods-link{
    	width: 170px;
    	display: -webkit-box;
    	display: -ms-flexbox;
    	display: flex;
		  -webkit-box-align: center;
		      -ms-flex-align: center;
		          align-items: center;
		/*flex-flow: row nowrap;
		justify-content: flex-start;*/
}
.category-detail .category-goods .goods-buy-link{
    	display: block;
    	width: 58px;
    	height: 22px;
    	line-height: 22px;
    	border: 1px solid #ff6700;
    	font-size: 12px;
		text-align: center;
		color: #ff6700;
}
.category-detail .category-goods .goods-buy-link:hover{
    	color: #fff;
    	background: #ff6700;
}
.topBanner_all .qy_lunboRight .el-carousel__item img{
      position:absolute;
      right:0;
}




