
.goods-left{
	float: left;
	width: 20%;
	height: auto;
}
.goods-left-item{
	width: 100%;
	height: 300px;
	margin-bottom: 14px;
	cursor: pointer;
	-webkit-transition: all 0.3s;
	transition:all 0.3s;
}
.goods-left-item:hover{
	-webkit-transform:translateY(-2px);
	        transform:translateY(-2px);
	-webkit-box-shadow: 5px 5px 20px #cccccc;
	        box-shadow: 5px 5px 20px #cccccc;
}
.goods-left-item img{
	width: 100%;
	height: 300px;
}
.item-bottom{
	margin-top: 14px;
}



