
.jm_chip.preload{
		background: url(//c1.mifile.cn/f/i/17/mitv4/65index/bg-chip.jpg) no-repeat center center;
}
.jm_chip{
		height: 1000px;
}
.jm_chip-container{
		text-align: center;
    	color: #fff;
    	width: 1226px;
    	margin-right: auto;
    	margin-left: auto;
}
.jm_chip-container .content{
		padding-top: 80px;
}
.jm_chip-container .title{
		top:40%;
		padding-bottom: 10px;
		opacity: 0;
		font-size: 40px;
		font-weight: 200;
		font-family: '仿宋';
		line-height: 64px;
		-webkit-transform:translate3d(0,0,0);
		        transform:translate3d(0,0,0);
		-webkit-transition: all .8s .2s;
		transition: all .8s .2s;
}
.jm_chip-container .item-des{
		-webkit-transition: all .8s .2s;
		transition: all .8s .2s;	
		top:20%;
		font-size: 16px;
		line-height: 30px;
}
.jm_chip-container .chip-list h2{
		font-size: 40px;
		line-height: 20px;
		font-weight: 400;
		opacity: 0;
		-webkit-transition: all .8s .2s;
		transition: all .8s .2s;
		top: 10%;
}
.jm_chip-container .chip-list .item:first-child{
		border-left: none;
}
.jm_chip-container .chip-list .item{
		display: inline-block;
		padding: 0 70px;
		border-left: 1px solid #fff;
		opacity: 0;
		-webkit-transform:translate3d(0,0,0);
		        transform:translate3d(0,0,0);
		margin-top: 50px;
		top:20%;
		-webkit-transition: all .8s .2s;
		transition: all .8s .2s;
}
.jm_chip-container .chip-list li{
		list-style: none;
}



