
.recommand_all{
  position: relative;
  width: 100%;
  height: 368px;
  text-align: center;
}
.recommand_all .qy_cal{
  position: relative;
  width: 90%;
  height: 100%;
  margin: auto;
}
.recommand_all .title{
  padding: 10px 0;
  text-align: left;
}
.recommand_all .qy_cal .cq_carousel{
  width: 450%;
  height: 300px;
}
.recommand_all .qy_cal .a{
  width: auto;
  height: 300px;
  overflow: hidden;
}
.recommand_all .qy_cal li{
  float: left;
  display: block;
  width: 4.3%;
  height: 300px;
  margin-right: 0.15%;
  font-size: 14px;
  background: #fff;
  cursor: pointer;
}
.recommand_all .qy_cal li:hover{
  -webkit-box-shadow: 5px 5px 20px #ccc;
          box-shadow: 5px 5px 20px #ccc;
  -webkit-transform: translateY(-2px);
          transform: translateY(-2px);
}
.recommand_all .qy_cal .lr_button{
  position: absolute;
  width: 100px;
  top: 6%;
  right: 0;
  text-align: right
}
.recommand_all .qy_cal .lr_button span{
  cursor: pointer;
  color: #666;
}
/*.recommand_all .qy_cal .lr_button span:hover{
  color: red
}*/
.recommand_all .cq_carousel h3{
  font-weight: normal;
  font-size: 14px;
}



