
body{
margin: 0;
}
.xm_topHeader{
width:100%;
height:100px;

position:relative
}
.xm_container{
width:90%;
height:100%;
/*background:blue;*/
display: -webkit-box;
display: -ms-flexbox;
display: flex;
margin:0 auto;
-webkit-box-pack: start;
    -ms-flex-pack: start;
        justify-content: flex-start;
-webkit-box-align: center;
    -ms-flex-align: center;
        align-items: center;
position:relative;
}
.xm_icon-mi{
width:55px;
height:55px;
background:url('../assets/img/icon-mi.png') no-repeat;

background-size: 100% 100%;
}
.xm_pic-gif{
width:163px;
height:66px;

background:url('../assets/img/win.gif') no-repeat;
margin-left:10px;
backgrounp-size:100% 100%
}
.navs{
height:88px;
margin: 0;
padding: 0;
}
.navs>li{
list-style:none;
display:inline-block;
/*float:left;*/
height:100%;
line-height:88px;
}
.navs>li>a{
text-decoration:none;
display:block;
padding:0px 10px;
color: black;
}
.xm_header-menu{
width:100%;
height:50px;

position:absolute;
top: 100px;
z-index:100px
}
.header-menu {
position: absolute;
left: 0;
top: 87px;
width: 100%;
height: 230px;
background: #fff;
-webkit-box-shadow: 0 1px 5px #ccc;
        box-shadow: 0 1px 5px #ccc;
z-index: 1000;
}
.menus {
list-style: none;
margin: 0;
padding: 30px 0 0 150px;
min-width: 1500px;
}
.product {
float:left;
}
.menus  .product  a {
display: block;
padding: 0 30px;
border-right: 1px solid #ccc;
}
.menus  .product  a:nth-last-child(1) {
   
border-right: 0;
}
.product-img {
width: 100%;
width: 160px;
height: 110px;
}
.product-name {
width: 100%;
height: 16px;
margin-top: 16px;
font-size: 12px;
text-align: center;
}
.product-price {
width: 100%;
height: 14px;
margin-top: 10px;
font-size: 10px;
text-align: center;
color: #ff6700;
}
.header-search {
position: absolute;
top: 23px;
right: 0;
width: 295px;
height: 50px;
border: 1px solid #e0e0e0;
}
.hot-word {
position: absolute;
top: 17px;
right: 62px;
z-index: 2;
text-align: right;
}
.hot-item {
float: left;
width: auto;
height: 18px;
line-height: 18px;
padding: 0 5px;
font-size: 12px;
background: #eee;
color: #757575;
margin-right: 5px;
cursor: pointer;
list-style:none;
}
.hot-item:hover {
color: #fff;
background: #ff6300;
}
.fadeOut-transition {
-webkit-transition: all .3s ease;
transition: all .3s ease;
opacity: 1;
}
.fadeOut-enter, .fadeOut-leave {
opacity: 0;
}
.search-ipt {
float: left;
width: 240px;
height: 100%;
border: 0;
outline: none;
}
.search-btn {
display: block;
float: right;
width: 50px;
height: 100%;
border-left: 1px solid #e0e0e0;
cursor: pointer;
}
.icon-search {
display: block;
width: 20px;
height: 20px;
margin-top: 15px;
margin-left: 15px;
background: url('../assets/img/icon-search.png') center center no-repeat;
background-size: 100% 100%;
}
.search-btn:hover{
background: #ff6700;
}
.search-btn:hover .icon-search {
background: url('../assets/img/icon-search-white.png') center center no-repeat;
background-size: 100% 100%;
}
.search-active {
border: 1px solid #ff6700;
}
.search-btn-active {
border-left: 1px solid #ff6700;
}
.search-result {
display: none;
position: absolute;
left: -1px;
top: 48px;
width: 245px;
height: auto;
margin: 0;
padding: 0;
list-style: none;
border: 1px solid #ff6700;
z-index: 11;
background: #fff;
}
.search-result li {
padding: 7px 13px;
width: 100%;
height: 30px;
-webkit-box-sizing: border-box;
        box-sizing: border-box;
cursor: pointer;
}
.item-name {
float: left;
font-size: 12px;
}
.item-num {
float: right;
font-size: 10px;
color: #b0b0b0;
}
   



