
.goods_collocation_all{
  width: 100%;
  height: 686px;
  background: #f5f5f5;
}
.collocation_container{
  width: 100%;
  height: 100%;
  /*border: 1px solid #000;*/
  margin: 0 auto;
}
.collocation_container .top{
  height: 58px;
  line-height: 58px;
  width: 90%;
  margin: 0 auto;
}
.collocation_container .top h3{
  float: left;
}
.collocation_container .top ul{
  float: right;
}
.collocation_container .top ul li{
  float: left;
  width: auto;
  height: 58px;
  line-height: 58px;
  margin-left: 30px;
  cursor: pointer;
  border-radius: 5px;
  /*border-bottom: 2px solid #f5f5f5;*/
}
.active{
  color: #ff6700;
  border-bottom: 2px solid #ff6700;
}
.bottom{
  width: 90%;
  margin: 0 auto;
}
.bottom .left{
  width: 20%;
  height: auto;
  float: left;
}
.bottom .left ul{
  width: 100%;
}
.bottom .left li{
  width: 100%;
  height: 300px;
  margin-bottom: 14px;
  background: #fff;
  -webkit-transition: all 0.3s;
  transition:all 0.3s;
}
.bottom .left li:hover,.bottom .right li:hover{
  -webkit-box-shadow: 5px 5px 20px #ccc;
          box-shadow: 5px 5px 20px #ccc;
  -webkit-transform: translateY(-2px);
          transform: translateY(-2px);
}
.bottom .left li img{
  width: 100%;
  height: 300px;
}
.bottom .right{
  height: 628px;
  width: 80%;
  float: left;
}
.bottom .right li{
  float: left;
  width: 23%;
  height: auto;
  padding: 20px 0;
  text-align: center;
  background: #fff;
  margin-left: 2%;
  margin-bottom: 3%
}
.bottom .right li img{
  display: block;
  width: 160px;
  height: 160px;
  margin: 0 auto;
}
.bottom .right li h3{
  font-weight: normal;
  font-size: 14px;
}
.bottom .right li .p1{
  color: #ff6700;
  font-size: 14px;
  text-align: center;
}
.bottom .right li .p2{
  margin: 0 10px;
  font-size: 12px;
  text-align: center;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  color: #b0b0b0;
}



