
.active{
      border-bottom:3px solid red;
}
.xm-goods{
    width:90%;
    height: 686px;
    margin: 0px auto;
}
.xm-goods-content{
    width:100%;
    height:628px;
}
.xm-goods-top{
    width: 100%;
    height: 58px;
}
.xm-goods .xm-goods-top h3 {
    float: left;
    margin: 0;
    font-size: 22px;
    font-weight: 200;
    line-height: 58px;
    color: #333;
}
.xm-goods-ul{
    float: right;
    list-style: none;
    margin: 0;
    padding: 0;
    margin-top: 16px;
}
ul{
    margin: 0;
    padding: 0;
}
.xm-goods-ul li{
    float: left;
    list-style: none;
    margin-left: 30px;
}
.xm-goods-ul li:hover{
     color: red;
}
.xm-left{
    width: 20%;
    height: 628px;
    float: left;
    display: inline-block;
}
.xm-left-li:hover{
    -webkit-transform:translateY(-2px);
            transform:translateY(-2px);
    -webkit-box-shadow:5px 5px 20px #ccc;
            box-shadow:5px 5px 20px #ccc;
}
.xm-left-li{
    width: 100%;
    height: 300px;
    list-style: none;
    margin-bottom: 14px;
    -webkit-transition:all .3s;
    transition: all .3s;
}
li a{
     text-decoration:none;
}
.xm-left-li a  img{
    width: 100%;
      height: 300px;
}
.xm-right{
    width: 80%;
    height: 628px;
     
    display: inline-block;
    float: left;
}
.goods-right-content{
    width:100%;
    height:100%;
}
.goods-right-item {
    width: 23%;
    height: 300px;
    cursor: pointer;
    -webkit-transition: all .3s;
    transition: all .3s;
    float: left;
    list-style: none;
    position: relative;
    margin: 0 0 14px 19px;
    padding: 20px 0;
    background :white;
    text-align: center;
}
.goods-right-item:hover{
    -webkit-transform:translateY(-1px);
            transform:translateY(-1px);
    -webkit-box-shadow:5px 5px 20px #ccc;
            box-shadow:5px 5px 20px #ccc;
}
.goods-right-item img{
      display: block;
      width: 160px;
      height: 160px;
      margin: 0 auto;
}
.goods-right-item .p-price{
      color:#ff6700;
      font-size:14px;
      text-align:center;
}
.goods-right-item .p-rank{
      color: #b0b0b0;
      font-size: 12px;
}
.xm-right h3{
      font-size: 14px;
      text-align: center;
      color: #333;
      font-weight: 400;
      text-overflow: ellipsis;
      white-space: nowrap;
      overflow: hidden;
}
.goods-right-item .discount-label {
    position: absolute;
    top: 0;
    left: 50%;
    width: 64px;
    height: 20px;
    line-height: 20px;
    margin-left: -32px;
    font-size: 12px;
    text-align: center;
    color: #fff;
    z-index: 4;
}
.new {
    background-color: #83c44e;
}
.discount {
    background-color: #e53935;
}
.review {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 234px;
    font-size: 12px;
    height: 0;
    line-height: 18px;
    overflow: hidden;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    color: #fff;
    background: #ff6700;
    opacity: 0;
    -webkit-transition: all .3s;
    transition: all .3s;
}
.review .review-content {
    display: block;
    text-align: center;
    margin: 8px 30px;
}
.review .review-author {
    display: block;
    text-align: center;
    margin-bottom: 8px;
}
.goods-right-item:hover .review {
    height: 76px;
    opacity: 1;
}
.goods-right-item:hover img {
       // transform: scale(1.4);
}



